







































































import {Component, Vue} from "vue-property-decorator";
import StockStatusDetailModal from "@/components/Stock/StockStatus/StockStatusDetailModal.vue";
import SelectBrand from "@/components/common/SelectBrand.vue";

@Component({
  components: {SelectBrand, StockStatusDetailModal},
})
export default class StockStatusTab extends Vue {
  searchParams = {...this.initSearchParams()};
  stockList = [];
  displayDetailModal = false;
  selectedStock = null;

  mounted(): void {
    this.getStockList();
  }

  reset() {
    this.searchParams = {...this.initSearchParams()};
  }

  getStockList(): void {
    this.api.getStockList(this.searchParams).then((res) => {
      this.stockList = res;
    });
  }

  initSearchParams() {
    return {
      item_number: null,
      brand_id: null,
      gte: null,
      lte: null,
    };
  }

  retrieveOrder(row) {
    this.selectedStock = row;
    this.displayDetailModal = true;
  }
}
