

































import {Component, Mixins, Prop, Watch} from "vue-property-decorator";
import AsModal from "@/mixins/AsModal";

@Component
export default class SeparateOrderModal extends Mixins(AsModal) {
  @Prop({type: Array, default: () => []}) readonly details!: any;

  tableData = [];
  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.tableData = this.details.map((detail) => {
        return {
          ...detail,
          count: 0,
        };
      });
    } else {
      this.tableData = [];
    }
  }
  submit(): void {
    const id = this.tableData[0].id;
    const specs = this.tableData.map((data) => {
      return {id: data.spec_id, count: data.count};
    });
    this.api.separateOrder({id, specs}).then(() => {
      this.dismiss();
      this.refresh();
    });
  }
}
