





































import ProductDetailSpec from "@/components/Product/ProductInfo/ProductDetailSpec.vue";
import SelectBrand from "@/components/common/SelectBrand.vue";
import SelectUnit from "@/components/common/SelectUnit.vue";
import SelectCategory from "@/components/common/SelectCategory.vue";
import {Component, Mixins, Prop, Watch} from "vue-property-decorator";
import AsModal from "@/mixins/AsModal";
import {initialAddProductRequest} from "@/core/initializer/initialAddProductRequest";
import { Product} from "@/types/api";
import {ProductSpec as ProductSpecType} from "@/types/api";
import {Action, Getter} from "vuex-class";
import {PRODUCT_SPEC_ACTION, ProductSpecActions} from "@/types/store/ProductSpec";

@Component({
  components: {SelectCategory, SelectUnit, SelectBrand, ProductDetailSpec},
})
export default class ProductDetail extends Mixins(AsModal) {
  @Getter("productSpec/productSpecList") readonly productSpecList!: ProductSpecType[];
  @Action(PRODUCT_SPEC_ACTION.CLEAR)
  clearSpecs!: ProductSpecActions["CLEAR"];

  @Prop({type: Object, default: () => ({...initialAddProductRequest()})}) readonly product!: any;

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.productFormData = this.product;
    } else {
      this.productFormData = {...initialAddProductRequest()};
    }
  }

  productFormData: {product: Product; specs: ProductSpecType[]} = {...initialAddProductRequest()};
}
