  deleteProductCategory () {
    if (this.productCategoryTreeCurrentNode.children) {
      this.$alert('當前選中類目含有子分類，請先刪除子分類！', '操作錯誤', {
        cancelButtonText: '確定',
        type: 'error'
      })
