      this.$confirm('確認要刪除此分類嗎？ 刪除後，此分類下的商品將會變為未分類商品！', '提示', {
        confirmButtonText: '確定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
      }).catch(() => {
      })
    }
  }
  resetForm () {
    this.$nextTick(() => {
      const ref: any = this.$refs['productFormRef']
      ref.resetFields()
    })
    this.productFormData = {
      name: '',
      spec: '',
      barcode: '',
      brand: '',
      status: '1'
