import {ProductCategoryTreeNode, ProductCategoryTreeNodes} from "@/types/ProductCategoryTreeNode";
import {ApiResponse, ProductCategory} from "@/types/api";

export function buildProductCategory(productCategoryList: ApiResponse<ProductCategory>[]): ProductCategoryTreeNodes {
  const tree: ProductCategoryTreeNode = {id: 0, name: "所有系統分類", children: []};

  const first = buildTreeNodeByLevel(productCategoryList, 1);
  const second = buildTreeNodeByLevel(productCategoryList, 2);
  const third = buildTreeNodeByLevel(productCategoryList, 3);

  setChildren(second, third);
  setChildren(first, second);

  tree.children = first;

  return [tree];
}

function buildTreeNodeByLevel(
  productCategoryList: ApiResponse<ProductCategory>[],
  level: number = 1,
): ProductCategoryTreeNodes {
  return productCategoryList
    .filter((product) => product.level === level)
    .map((product) => {
      return {...product, children: []};
    });
}

function setChildren(parents: ProductCategoryTreeNodes, children: ProductCategoryTreeNodes): void {
  parents.forEach((category) => {
    category.children = children.filter((child) => child.parent_id === category.id);
  });
}
