
















































































import { Component, Emit, Prop, Vue, Mixins, Watch } from 'vue-property-decorator'
import {formatDate} from "@/core/utils/formatDate";
import {DateFormat} from "@/core/constants/DateFormat";
import SelectProductDialog from '@/components/Product/SelectProductDialog.vue'
import AsModal from "@/mixins/AsModal";
import {initialCreateStockTransfer} from "@/core/initializer/initialCreateStockTransfer";
import {CreateStockTransferRequest} from "@/types/api/request";
import {ApiResponse, CommonResponse, Supplier, Warehouse} from "@/types/api";


@Component({
    components: { SelectProductDialog }
})
export default class AddStockTransferDialog extends Mixins(AsModal) {
  displaySelectProductDialog: boolean = false;
  @Prop(Boolean) readonly visible!: boolean;
  @Emit('dismiss') dismiss (): void {}
  @Emit('update:visible') close (): boolean {
    return false
  }

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.api.getWarehouseList().then((res) => (this.warehouseList = res));
      this.api.getAllocateCategoryList().then((res) => (this.allocateCategoryList = res));
    } else {
      this.stockTransferFormData = {...initialCreateStockTransfer};
      this.productList = [];
    }
  }

  displayStockTransferDetailDialog: boolean = false;
  warehouseList: ApiResponse<Warehouse>[] = [];
  allocateCategoryList: CommonResponse = [];
  productList = [];
  stockTransferFormData: CreateStockTransferRequest = {...initialCreateStockTransfer};

  updateSelectedProducts(selected: any[]): void {
    this.productList = selected;
    this.displaySelectProductDialog = false;
  }

  displayTotalPrice(price, amount):number {
    if( Number.isNaN(amount)) {
      amount = 0;
    }
    return price * amount 
  }

  purchaseTablePaginationData = {
    totalRows: 400,
    currentPage: 1,
    currentPageSize: 20,
    pageSizes: [20, 50, 100, 200]
  }

  getSummary({ columns, data }) {
    const sums = [];
    columns.forEach((columns, index) => {
      if (index === 0){
        sums[index] = "合計";
      }
      else if (index === 6) {
        sums[index] = data.reduce((prev, curr) => {
          const result = prev + Number(curr.amount)
          return (
            Number.isNaN(result) ? 0 : result
          );
        }, 0);
      }
      else if (index === 8) {
        sums[index] = data.reduce((prev, curr) => {
          const result = prev + (curr.wholesale_price * curr.amount)
          return (
            Number.isNaN(result) ? 0 : result
          );
        }, 0);
      }
      else
      {
        sums[index] = "";
      }
    });
    return sums;
  }

  deleteProduct(index) {
    this.productList.splice(index, 1);
  }

  save(): void {
    this.stockTransferFormData.business_date = formatDate(
      this.stockTransferFormData.business_date,
      DateFormat.YMDDashed,
    );
    const specs = this.productList.map((product)=> {return {
      spec_id: product.id,
      count: Number(product.amount)
    }})
    const params = {allocate: this.stockTransferFormData, specs}
    this.api.addStockTransfer(params).then(() => {
      this.refresh();
      this.dismiss();
    });
  }

  resetForm() {
    this.$nextTick(() => {
      const ref: any = this.$refs["stockTransferFormRef"];
      ref.resetFields();
    });
    this.stockTransferFormData = {...initialCreateStockTransfer};
  }

}
