import {VuexActions, VuexMutations} from "@/types/store/VuexTypes";
import {GetterTree} from "vuex";
import {PRODUCT_SPEC_MUTATION, ProductSpec, ProductSpecActions, ProductSpecMutations} from "@/types/store/ProductSpec";
import {ProductSpec as ProductSpecType} from "@/types/api";
const namespaced = true;

const state: ProductSpec = {
  productSpecList: [],
};

const getters: GetterTree<ProductSpec, any> = {
  productSpecList: ({productSpecList}) => productSpecList,
};

const mutations: VuexMutations<ProductSpecMutations, ProductSpec> = {
  ADD_PRODUCT_SPEC: (store, spec: ProductSpecType) => {
    store.productSpecList = [...store.productSpecList, spec];
  },
  DELETE_PRODUCT_SPEC(store, index: number): void {
    store.productSpecList.splice(index, 1);
  },
  CLEAR(store): void {
    store.productSpecList = [];
  },
};

const actions: VuexActions<ProductSpecActions, ProductSpec, any> = {
  ADD_PRODUCT_SPEC: (context, spec) => {
    context.commit(PRODUCT_SPEC_MUTATION.ADD_PRODUCT_SPEC, spec);
  },
  DELETE_PRODUCT_SPEC: (context, index: number) => {
    context.commit(PRODUCT_SPEC_MUTATION.DELETE_PRODUCT_SPEC, index);
  },
  CLEAR(context): void {
    context.commit(PRODUCT_SPEC_MUTATION.CLEAR);
  },
};

export default {
  namespaced,
  state,
  getters,
  actions,
  mutations,
};
