
















import {Component, Mixins, Prop, Watch} from "vue-property-decorator";
import {ProductCategoryTreeNode} from "@/types/ProductCategoryTreeNode";
import AsModal from "@/mixins/AsModal";

@Component({})
export default class EditProductCategoryDialog extends Mixins(AsModal) {
  @Prop(Object) readonly category!: ProductCategoryTreeNode;

  form: {name: string} = {name: ""};

  submit(): void {
    this.api.updateProductCategoryList(this.form, this.category.id).then(() => {
      this.refresh();
      this.dismiss();
    });
  }

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.form = {name: this.category.name};
    } else {
      this.form = {name: ""};
    }
  }
}
