











































































































import {Component, Vue} from "vue-property-decorator";
import {buildProductCategory} from "@/core/builder/buildProductCategory";
import {ProductCategoryTreeNode, ProductCategoryTreeNodes} from "@/types/ProductCategoryTreeNode";
import {GetProductListResponse} from "@/types/api/response";
import {GetProductListRequest} from "@/types/api/request/GetProductListRequest";
import {AddProductRequest} from "@/types/api/request";
import {initialAddProductRequest} from "@/core/initializer/initialAddProductRequest";
import {CommonResponse} from "@/types/api";

@Component({})
export default class StockAlertTab extends Vue {
  readonly treeProps = {label: "name"};
  copyProductData: AddProductRequest = {...initialAddProductRequest()};
  productCategoryTreeCurrentNode: ProductCategoryTreeNode = {id: 0, name: "所有系統分類", children: []};
  productCategoryTreeCurrentNodeId: number = 0;
  productCategoryData: ProductCategoryTreeNodes = [];
  productBrandList: CommonResponse = [];
  productFormData: GetProductListRequest = {
    name: "",
    spec: "",
    barcode: "",
    brand: null,
    status: "Y",
  };
  productTableData: GetProductListResponse = [];
  selectedProduct: GetProductListResponse = [];
  productTablePaginationData = {
    totalRows: 400,
    currentPage: 1,
    currentPageSize: 20,
    pageSizes: [20, 50, 100, 200],
  };

  afterProductAction(): void {
    this.getProductList();
    this.copyProductData = {...initialAddProductRequest()};
  }

  handleSelectionChange(selectedProduct: GetProductListResponse): void {
    this.selectedProduct = selectedProduct;
  }

  mounted(): void {
    this.getProductList();
    this.getProductCategoryList();
    this.api.getProductBrandList().then((res) => (this.productBrandList = res));
  }

  getProductList(): void {
    this.api.getProductList(this.productFormData).then((res) => (this.productTableData = res));
  }

  enableProduct(): void {
    this.selectedProduct.forEach((product) => {
      this.api
        .updateProduct({status: "Y"}, product.id)
        .then(() => {
          this.selectedProduct = [];
          this.getProductList();
        })
        .catch(console.error);
    });
  }

  disableProduct(): void {
    this.selectedProduct.forEach((product) => {
      this.api
        .updateProduct({status: "N"}, product.id)
        .then(() => {
          this.selectedProduct = [];
          this.getProductList();
        })
        .catch(console.error);
    });
  }

  getProductCategoryList(): void {
    this.api.getProductCategoryList().then((res) => {
      this.productCategoryData = buildProductCategory(res);
    });
  }

  handleCurrentChange(node: any) {
    this.productCategoryTreeCurrentNode = node;
    this.productCategoryTreeCurrentNodeId = node.id;
  }

  resetForm() {
    this.$nextTick(() => {
      const ref: any = this.$refs["productFormRef"];
      ref.resetFields();
    });
    this.productFormData = {
      name: "",
      spec: "",
      barcode: "",
      brand: null,
      status: "Y",
    };
  }
}
