








































import {Component, Vue} from "vue-property-decorator";
import AddImageDialog from "@/components/Image/AddImageDialog.vue";
import AddProductSpecDialog from "@/components/Product/ProductInfo/AddProductSpecDialog.vue";
import {Action, Getter} from "vuex-class";
import {ProductSpec as ProductSpecType} from "@/types/api";
import {PRODUCT_SPEC_ACTION, ProductSpecActions} from "@/types/store/ProductSpec";

@Component({
  components: {AddProductSpecDialog, AddImageDialog},
})
export default class ProductSpec extends Vue {
  @Getter("productSpec/productSpecList") readonly productSpecList!: ProductSpecType[];
  @Action(PRODUCT_SPEC_ACTION.DELETE_PRODUCT_SPEC)
  deleteSpec!: ProductSpecActions["DELETE_PRODUCT_SPEC"];
  displayAddProductSpecDialog: boolean = false;
  displayAddImageDialog: boolean = false;
}
