






















































































































import AddPurchaseDialog from "@/components/Purchase/PurchaseOrder/AddPurchaseDialog.vue";
import PurchaseDetail from "@/components/Purchase/PurchaseOrder/PurchaseDetail.vue";
import {formatTimeRange} from "@/core/utils/formatTimeRange";
import {Component, Vue} from "vue-property-decorator";
import {GetPurchaseListResponse} from "@/types/api/response";
import SelectWarehouse from "@/components/common/SelectWarehouse.vue";
import SelectSupplier from "@/components/common/SelectSupplier.vue";
import SelectSettlement from "@/components/common/SelectSettlement.vue";
import SelectSales from "@/components/common/SelectSales.vue";

@Component({
  components: {SelectSales, SelectSettlement, SelectSupplier, SelectWarehouse, AddPurchaseDialog, PurchaseDetail},
})
export default class PurchaseOrderTab extends Vue {
  displayAddPurchaseDialog: boolean = false;
  displayPurchaseDetailDialog: boolean = false;
  purchaseDetail = null;
  productList = [];
  searchDate = null;

  mounted(): void {
    this.api.getProductList().then((res) => (this.productList = res));
    this.getData();
  }

  getData(): void {
    const searchDate = formatTimeRange(this.searchDate);
    const timeParams = {gte: searchDate[0], lte: searchDate[1]};
    const params = {...this.purchaseOrderFormData, ...timeParams};
    this.api.getPurchaseList(params).then((res) => {
      this.purchaseTableData = res;
    });
  }

  getDetail(id: number): void {
    this.api.getPurchase(id).then((res) => {
      this.purchaseDetail = res;
      this.displayPurchaseDetailDialog = true;
    });
  }

  purchaseTableData: GetPurchaseListResponse = [];
  purchaseOrderFormData = {
    code: null,
    supply_id: null,
    warehouse_id: null,
    status: null,
    settlement_id: null,
    saleman_id: null,
  };
}
