























import { Component, Emit, Prop, Vue } from 'vue-property-decorator'

@Component({})
export default class AddProductLocationDialog extends Vue {
  @Prop(Boolean) readonly visible!: boolean;
  @Emit('dismiss') dismiss (): void {}
  @Emit('update:visible') close (): boolean {
    return false
  }

  productLocationFormData = {
    name: ''
  };

  resetForm () {
    this.$nextTick(() => {
      const ref: any = this.$refs['productLocationFormRef']
      ref.resetFields()
    })
    this.productLocationFormData = {
      name: ''
    }
  }
}
