














































import PermissionSetting from '@/components/Setting/PermissionSetting.vue'
import { Component, Vue } from 'vue-property-decorator'

@Component({
    components: { PermissionSetting }
})
export default class RoleManagementTab extends Vue {
  currentRoleId = 1
  roleTableData = [
    { id: 1, name: '倉庫'},
    { id: 2, name: '客服'},
    { id: 3, name: '行銷'},
  ];

  addRole (scope: any) {
    this.roleTableData.push({id: this.roleTableData.length+1, name: ''})
  }
  handleCurrentChange (val: any) {
    this.currentRoleId = val.id
  }
}
