










































































import AddSupplierDialog from "@/components/CRM/SupplierManagement/AddSupplierDialog.vue";
import EditSupplierDialog from "@/components/CRM/SupplierManagement/EditSupplierDialog.vue";
import {Component, Vue} from "vue-property-decorator";
import {ApiResponse, Supplier} from "@/types/api";

@Component({
  components: {EditSupplierDialog, AddSupplierDialog},
})
export default class SupplierManagementTab extends Vue {
  displayAddSupplierDialog: boolean = false;
  displayEditSupplierDialog: boolean = false;
  supplyList: ApiResponse<Supplier>[] = [];
  selectedId: number = 0;

  mounted(): void {
    this.getData();
  }

  getData(): void {
    this.selectedId = 0;
    this.api.getSupplyList().then((res) => {
      this.supplyList = res;
    });
  }

  editSupplier(id: number): void {
    this.selectedId = id;
    this.displayEditSupplierDialog = true;
  }

  copySupplier(id: number): void {
    this.selectedId = id;
    this.displayAddSupplierDialog = true;
  }

  productFormData = {
    name: "",
    spec: "",
    barcode: "",
    brand: "",
    status: "1",
  };
}
