



































































































































































































































import SelectProductDialog from '@/components/Product/SelectProductDialog.vue'
import {Component, Emit, Prop, Vue} from 'vue-property-decorator'

@Component({
    components: {SelectProductDialog}
})
export default class AddQuickProductMixDialog extends Vue {
    @Prop(Boolean) readonly visible!: boolean;

    @Emit('dismiss') dismiss(): void {
    }

    @Emit('update:visible') close(): boolean {
        return false
    }

    displaySelectProductDialog: boolean = false;
    productFormData = {
        productId: '',
        productNumber: '',
        name: '',
        category: '',
        brand: '',
        note: '',
        standardPrice: '',
        wholesalePrice: '',
        barcode: ''
    };

    resetForm() {
        this.$nextTick(() => {
            const ref: any = this.$refs['productFormRef']
            ref.resetFields()
        })
        this.productFormData = {
            productId: '',
            productNumber: '',
            name: '',
            category: '',
            brand: '',
            note: '',
            standardPrice: '',
            wholesalePrice: '',
            barcode: ''
        }
    }
}
