



































































import {Component, Mixins, Prop, Watch} from "vue-property-decorator";
import AsModal from "@/mixins/AsModal";
import {initialSupplyRequest} from "@/core/initializer/initialAddSupplyRequest";
import {Supplier} from "@/types/api";

@Component({})
export default class AddSupplierDialog extends Mixins(AsModal) {
  @Prop({type: Number, default: 0}) readonly id!: number;

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val && this.id !== 0) {
      this.api.getSupply(this.id).then((res) => {
        this.formData = {...this._.omit(res, ["id", "created_at", "updated_at"])};
      });
    } else {
      this.refresh();
      this.formData = {...initialSupplyRequest()};
    }
  }

  formData: Supplier = {...initialSupplyRequest()};

  save(): void {
    this.api.addSupply(this.formData).then(() => {
      this.refresh();
      this.formData = {...initialSupplyRequest()};
      this.dismiss();
    });
  }
}
