








































import {Component, Vue} from "vue-property-decorator";
import {ApiResponse, CommonResponse} from "@/types/api";

@Component
export default class UnitManagementTab extends Vue {
  unitList: CommonResponse = [];

  mounted(): void {
    this.getData();
  }

  getData(): void {
    this.api.getProductUnitList().then((res) => (this.unitList = res));
  }

  addUnit(): void {
    this.$prompt("單位", "新增單位", {
      confirmButtonText: "確定",
      cancelButtonText: "取消",
    })
      .then((action: any) => {
        this.api.addUnit(action.value).then(() => this.getData());
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "取消!",
        });
      });
  }

  editUnit(unit: ApiResponse<{name: string}>): void {
    this.$prompt("單位", "修改單位", {
      confirmButtonText: "確定",
      cancelButtonText: "取消",
      inputValue: unit.name,
    })
      .then((action: any) => {
        this.api.editUnit(action.value, unit.id).then(() => this.getData());
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "取消!",
        });
      });
  }

  selector = {
    unit: "",
    status: "enable",
  };
}
