








import AsInputable from "@/mixins/AsInputable";
import {Component, Mixins, Prop} from "vue-property-decorator";
import {CommonResponse} from "@/types/api";

@Component
export default class SelectBrand extends Mixins(AsInputable) {
  @Prop(Number) readonly value!: string;
  @Prop({type: String, default: "品牌"}) readonly label!: string;

  brandList: CommonResponse = [];

  beforeMount(): void {
    this.api.getProductBrandList().then((res) => (this.brandList = res));
  }

  get brandOptions(): CommonResponse {
    const all: CommonResponse = [{name: "所有品牌", id: null}];
    return this.withAll ? all.concat(this.brandList) : this.brandList;
  }
}
