





































































































































































































































import {Component, Vue} from "vue-property-decorator";
import {GetOrderListResponse} from "@/types/api/response";
import {ApiResponse, Supplier} from "@/types/api";
import SelectWarehouse from "@/components/common/SelectWarehouse.vue";
import SelectPlatform from "@/components/common/SelectPlatform.vue";
import SeparateOrderModal from "@/components/order/SeparateOrderModal.vue";

@Component({
  components: {SeparateOrderModal, SelectPlatform, SelectWarehouse},
})
export default class ReviewOrderTab extends Vue {
  supplierList: ApiResponse<Supplier>[] = [];
  orderDetail = [];
  salesList = [];
  productList = [];
  settleList = [];
  selectedOrders = [];
  displaySeparateOrderModal: boolean = false;

  mounted(): void {
    this.api.getProductList().then((res) => (this.productList = res));
    this.api.getSupplyList().then((res) => (this.supplierList = res));
    this.api.getSalesList().then((res) => (this.salesList = res));
    this.getData();
  }

  getData(): void {
    const params = {...this.orderFormData, status: 0};
    this.api.getOrderList(params).then((res) => {
      this.orderTableData = res;
    });
  }

  orderTableData: GetOrderListResponse = [];

  orderFormData = {
    brand: null,
    code: null,
    saleman_id: null,
    number: null,
    product: null,
    warehouse_id: null,
    platform_id: null,
    print: null,
    logistics: null,
  };

  reset() {
    this.orderFormData = {
      brand: null,
      code: null,
      saleman_id: null,
      number: null,
      product: null,
      warehouse_id: null,
      platform_id: null,
      print: null,
      logistics: null,
    };
  }

  closeOrder(id) {
    this.$confirm("確認要關閉此訂單嗎？", "關閉訂單", {
      confirmButtonText: "確定",
      cancelButtonText: "取消",
      type: "warning",
    })
      .then(() => {
        //status = -1 暫定為 關閉
        this.api.setOrderStatus(id, -1).then(() => this.getData());
      })
      .catch(() =>
        this.$message({
          type: "info",
          message: "取消!",
        }),
      );
  }

  confirmOrder() {
    // status = 1 為 待出貨
    const orders = this.selectedOrders.map((order) => {
      return {id: order.id, status: 1};
    });
    this.api.setOrdersStatus(orders).then(() => {
      this.selectedOrders = [];
      this.getData();
    });
  }

  abnormalOrder() {
    // status = 3 為 異常
    const orders = this.selectedOrders.map((order) => {
      return {id: order.id, status: 3};
    });
    this.api.setOrdersStatus(orders).then(() => {
      this.selectedOrders = [];
      this.getData();
    });
  }

  get canConfirmAndAbnormal() {
    return this.selectedOrders.length > 0;
  }

  get canMergeOrder() {
    const noMerged = !this.selectedOrders.some((order) => order.is_merge === 1);
    const moreThenOne = this.selectedOrders.length > 1;
    const receiver = this.selectedOrders[0] && this.selectedOrders[0].receiver;
    const address = this.selectedOrders[0] && this.selectedOrders[0].address;
    const sameReceiver = this.selectedOrders.every((order) => order.receiver === receiver);
    const sameAddress = this.selectedOrders.every((order) => order.address === address);
    return noMerged && moreThenOne && sameReceiver && sameAddress;
  }

  get canSeparated(): boolean {
    return this.selectedOrders.length === 1;
  }

  get canReverseMergeOrder() {
    const merged = this.selectedOrders.every((order) => order.is_merge === 1);
    const isSelected = this.selectedOrders.length > 0;
    return merged && isSelected;
  }

  onSelectionChange(selected: any[]) {
    this.selectedOrders = selected;
  }

  selectMerged(command) {
    if (command === "mergeOrder") {
      this.mergeOrder();
    }
    if (command === "reverseMerge") {
      this.reverseMerge();
    }

    if (command === "separate") {
      this.separateOrder();
    }
  }

  separateOrder() {
    this.api.getOrderProductSpecs(this.selectedOrders[0].id).then((res) => {
      this.orderDetail = res;
      this.displaySeparateOrderModal = true;
    });
  }

  reverseMerge() {
    const ids = this.selectedOrders.map((order) => {
      return order.id;
    });
    this.api.reverseMergeOrder({ids}).then(() => {
      this.selectedOrders = [];
      this.getData();
    });
  }

  mergeOrder() {
    const ids = this.selectedOrders.map((order) => {
      return order.id;
    });
    this.api.mergeOrder({ids}).then(() => {
      this.selectedOrders = [];
      this.getData();
    });
  }

  getOrderProductSpecs(row) {
    this.api.getOrderProductSpecs(row.id).then((res) => {
      this.orderDetail = res;
    });
  }
}
