









































































































































































































import {Component, Vue} from "vue-property-decorator";
import SelectPlatform from "@/components/common/SelectPlatform.vue";
import SelectWarehouse from "@/components/common/SelectWarehouse.vue";
@Component({
  components: {SelectWarehouse, SelectPlatform},
})
export default class ToBeShippedOrderTab extends Vue {
  supplierList = [];
  orderDetail = [];
  salesList = [];
  productList = [];
  settleList = [];
  selectedOrders = [];
  orderTableData = [];
  orderFormData = {
    brand: null,
    code: null,
    saleman_id: null,
    number: null,
    product: null,
    warehouse_id: null,
    platform_id: null,
    print: null,
    logistics: null,
  };

  mounted(): void {
    this.api.getProductList().then((res) => (this.productList = res));
    this.api.getSupplyList().then((res) => (this.supplierList = res));
    this.api.getSalesList().then((res) => (this.salesList = res));
    this.getData();
  }
  getData(): void {
    const params = {...this.orderFormData, status: 1};
    this.api.getOrderList(params).then((res) => {
      this.orderTableData = res;
    });
  }

  reset() {
    this.orderFormData = {
      brand: null,
      code: null,
      saleman_id: null,
      number: null,
      product: null,
      warehouse_id: null,
      platform_id: null,
      print: null,
      logistics: null,
    };
  }
  getOrderProductSpecs(row) {
    this.api.getOrderProductSpecs(row.id).then((res) => {
      this.orderDetail = res;
    });
  }

  onSelectionChange(selected: any[]) {
    this.selectedOrders = selected;
  }

  get canConfirmAndAbnormal() {
    return this.selectedOrders.length > 0;
  }

  abnormalOrder() {
    // status = 3 為 異常
    const orders = this.selectedOrders.map((order) => {
      return {id: order.id, status: 3};
    });
    this.api.setOrdersStatus(orders).then(() => {
      this.selectedOrders = [];
      this.getData();
    });
  }

  backToReview() {
    // status = 0 為 審核
    const orders = this.selectedOrders.map((order) => {
      return {id: order.id, status: 0};
    });
    this.api.setOrdersStatus(orders).then(() => {
      this.selectedOrders = [];
      this.getData();
    });
  }

  selectDropdown(command) {
    if (command === "backToReview") {
      this.backToReview();
    }
  }

  confirmShip() {
    const ids = this.selectedOrders.map((order) => {
      return order.id;
    });

    this.api.shipOrder({ids}).then(() => {
      this.selectedOrders = [];
      this.getData();
    });
  }
}
