























import {Component, Mixins, Prop} from "vue-property-decorator";
import {ProductCategoryTreeNode} from "@/types/ProductCategoryTreeNode";
import AsModal from "@/mixins/AsModal";
import {ProductCategory} from "@/types/api";

@Component({})
export default class AddProductCategoryDialog extends Mixins(AsModal) {
  @Prop(Object) readonly parent!: ProductCategoryTreeNode;

  get initialProductCategoryFormData(): ProductCategory {
    return {
      name: "",
      parent_id: 0,
      level: null,
    };
  }

  productCategoryFormData: ProductCategory = {...this.initialProductCategoryFormData};

  submit(e): void {
    const isFirst = this.parent.id === 0;
    this.productCategoryFormData.parent_id = isFirst ? null : this.parent.id;
    this.productCategoryFormData.level = isFirst ? 1 : this.parent.level + 1;
    this.api.addProductCategoryList(this.productCategoryFormData).then(() => {
      this.dismiss();
      this.refresh();
    });
  }

  resetForm() {
    this.$nextTick(() => {
      const ref: any = this.$refs["productCategoryFormRef"];
      ref.resetFields();
    });
    this.productCategoryFormData = {...this.initialProductCategoryFormData};
  }
}
