




























































import SelectOneProductDialog from "@/components/Product/SelectOneProductDialog.vue";

import {Component, Vue} from "vue-property-decorator";

@Component({
  components: {SelectOneProductDialog},
})
export default class ProductCorrespondenceTab extends Vue {
  displaySelectEditProductDialog: boolean = false;
  displaySelectAddProductDialog: boolean = false;

  searchParams = {
    code: null,
    correspond_name: null,
    spec_name: null,
  };

  relationList = [];
  params = {spec_id: null, code: null, correspond_name: null};
  editId = null;

  onAdd() {
    this.params = {spec_id: null, code: null, correspond_name: null};
    this.$prompt("請輸入線上商品編碼", "新增", {
      confirmButtonText: "確定",
      cancelButtonText: "取消",
    })
      .then((action: any) => {
        this.params.code = action.value;
        this.$prompt("請輸入線上商品名稱", "新增", {
          confirmButtonText: "確定",
          cancelButtonText: "取消",
        })
          .then((action: any) => {
            this.params.correspond_name = action.value;
            this.displaySelectAddProductDialog = true;
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "取消新增",
            });
          });
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "取消新增",
        });
      });
  }

  onSelectAdd(selected) {
    this.params.spec_id = selected.id;
    this.api.addProductCorrespond(this.params).then(() => {
      this.resetParams();
      this.search();
    });
  }

  onSelectChangeSpec(id) {
    this.editId = id;
    this.displaySelectEditProductDialog = true;
  }

  onSelectChangeName(selected) {
    this.editId = selected.id;
    this.$prompt("修改線上商品編碼", "修改", {
      confirmButtonText: "確定",
      cancelButtonText: "取消",
      inputPlaceholder: selected.code,
    })
      .then((action: any) => {
        const params = {...selected, code: action.value};
        this.api.changeProductCorrespond(this.editId, params).then(() => {
          this.resetParams();
          this.search();
        });
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "取消修改",
        });
      });
  }

  onSelectEdit(selected) {
    this.api.changeProductCorrespond(this.editId, {spec_id: selected.id}).then(() => {
      this.resetParams();
      this.search();
    });
  }

  search() {
    this.api.getProductCorrespondList(this.searchParams).then((res) => (this.relationList = res));
  }

  resetParams() {
    this.searchParams = {
      code: null,
      correspond_name: null,
      spec_name: null,
    };
  }

  deleteRelation(id) {
    this.api.deleteProductCorrespond(id).then(() => {
      this.resetParams();
      this.search();
    });
  }

  mounted() {
    this.search();
  }
}
