import _ from "lodash";
import {formatDate} from "@/core/utils/formatDate";

const requiredHeaders = [
  "訂單號碼",
  "備註",
  "數量",
  "成本",
  "配送地址",
  "客戶名稱",
  "商品編號",
  "客戶電話",
  "室內電話",
];

export function parseImportOrder(header, body) {
  if (checkAllRequiredPass(header)) {
    const mapTable = buildMapTable(header);
    const parsedBody = body.map((data) => {
      return {
        code: data[mapTable.code],
        remark: data[mapTable.remark],
        amount: data[mapTable.amount],
        cost: data[mapTable.cost],
        address: data[mapTable.address],
        receiver: data[mapTable.receiver],
        productId: data[mapTable.productId],
        phone: data[mapTable.phone],
        tel: data[mapTable.tel],
      };
    });
    const totalBody = _.uniqBy(parsedBody, (d: any) => d.code).map((data) => {
      return _.omit(data, ["amount", "cost", "productId"]);
    });
    const bodyWithProduct = totalBody.map((data) => {
      const specs = parsedBody
        .filter((parsed) => parsed.code === data.code)
        .map((filter) => {
          return {
            count: filter.amount,
            cost: filter.cost,
            id: filter.productId,
          };
        });
      const totalPrice = specs.reduce((acc, current) => {
        return acc + current.count * current.cost;
      }, 0);
      return {order: {...data}, specs, totalPrice};
    });
    return bodyWithProduct;
  }
  return [];
}

function checkAllRequiredPass(header): boolean {
  const allCheck = requiredHeaders.map((required) => header.includes(required));
  return allCheck.findIndex((check) => !check) === -1;
}

function buildMapTable(header) {
  const code = header.findIndex((item) => item === requiredHeaders[0]);
  const remark = header.findIndex((item) => item === requiredHeaders[1]);
  const amount = header.findIndex((item) => item === requiredHeaders[2]);
  const cost = header.findIndex((item) => item === requiredHeaders[3]);
  const address = header.findIndex((item) => item === requiredHeaders[4]);
  const receiver = header.findIndex((item) => item === requiredHeaders[5]);
  const productId = header.findIndex((item) => item === requiredHeaders[6]);
  const phone = header.findIndex((item) => item === requiredHeaders[7]);
  const tel = header.findIndex((item) => item === requiredHeaders[8]);
  return {code, remark, amount, cost, address, receiver, productId, phone, tel};
}

export function translateOrderToParams(orders, platform_id) {
  const noPriceOrder = orders.map((item) => {
    const order = {...item.order, platform_id, type: 2, place_time: formatDate(new Date(), "YYYY-MM-DD")};
    const [postal_code, address] = order.address.split(" ");
    const specs = item.specs.map((spec) => _.omit(spec, ["cost"]));
    return {order: {...order, postal_code, address}, specs};
  });

  return noPriceOrder;
}
