













import AsInputable from "@/mixins/AsInputable";
import {Component, Mixins, Prop} from "vue-property-decorator";
import {CommonResponse} from "@/types/api";

@Component
export default class SelectPlatform extends Mixins(AsInputable) {
  @Prop(Number) readonly value!: string;
  @Prop({type: String, default: "平台"}) readonly label!: string;

  platformList: CommonResponse = [];

  beforeMount(): void {
    this.api.getPlatformList().then((res) => (this.platformList = res));
  }

  get platformOptions(): CommonResponse {
    const all: CommonResponse = [{name: "全部", id: null}];
    return this.withAll ? all.concat(this.platformList) : this.platformList;
  }
}
