











































































































import SelectProductDialog from "@/components/Product/SelectProductDialog.vue";
import AddPurchaseInDialog from "@/components/Purchase/PurchaseInOrder/AddPurchaseInDialog.vue";
import AddReviewInDialog from "@/components/Purchase/PurchaseInOrder/AddReviewInDialog.vue";
import {Component, Vue} from "vue-property-decorator";
import {formatTimeRange} from "@/core/utils/formatTimeRange";
import SelectWarehouse from "@/components/common/SelectWarehouse.vue";
import SelectSupplier from "@/components/common/SelectSupplier.vue";
import SelectSales from "@/components/common/SelectSales.vue";

@Component({
  components: {
    SelectSales,
    SelectSupplier,
    SelectWarehouse,
    SelectProductDialog,
    AddPurchaseInDialog,
    AddReviewInDialog,
  },
})
export default class PurchaseInOrderTab extends Vue {
  displaySelectProductDialog: boolean = false;
  displayAddPurchaseInDialog: boolean = false;
  displayAddReviewInDialog: boolean = false;
  purchaseOrderFormData = {
    supply_id: null,
    warehouse_id: null,
    saleman_id: null,
  };

  purchaseInList = [];
  searchDate = null;

  tt;
  mounted(): void {
    this.getData();
  }
  getData(): void {
    const searchDate = formatTimeRange(this.searchDate);
    const timeParams = {gte: searchDate[0], lte: searchDate[1]};
    const params = {...this.purchaseOrderFormData, ...timeParams};
    this.api.getPurchaseInList(params).then((res) => {
      this.purchaseInList = res;
    });
  }

  reset(): void {
    this.purchaseOrderFormData = {
      supply_id: null,
      warehouse_id: null,
      saleman_id: null,
    };
    this.searchDate = null;
  }
}
