























































import ProductMixSpec from '@/components/Product/ProductMix/ProductMixSpec.vue'
import ProductCustomsDeclaration from '@/components/Product/ProductMix/ProductCustomsDeclaration.vue'
import { Component, Emit, Prop, Vue } from 'vue-property-decorator'

@Component({
  components: { ProductMixSpec, ProductCustomsDeclaration }
})
export default class AddProductMixDialog extends Vue {
  @Prop(Boolean) readonly visible!: boolean;
  @Emit('dismiss') dismiss (): void {}
  @Emit('update:visible') close (): boolean {
    return false
  }

  productFormData = {
    productId: '',
    productNumber: '',
    name: '',
    category: '',
    brand: '',
    note: '',
    standardPrice: '',
    wholesalePrice: '',
    barcode: ''
  };

  productTablePaginationData = {
    totalRows: 400,
    currentPage: 1,
    currentPageSize: 20,
    pageSizes: [20, 50, 100, 200]
  }

  resetForm () {
    this.$nextTick(() => {
      const ref: any = this.$refs['productFormRef']
      ref.resetFields()
    })
    this.productFormData = {
      productId: '',
      productNumber: '',
      name: '',
      category: '',
      brand: '',
      note: '',
      standardPrice: '',
      wholesalePrice: '',
      barcode: ''
    }
  }
}
