


































































































































































































import { Component, Vue, Watch } from 'vue-property-decorator'
import {formatDate} from "@/core/utils/formatDate";
import {DateFormat} from "@/core/constants/DateFormat";
import {initialCreateOrder} from "@/core/initializer/initialCreateOrder";
import {CreateOrderRequest} from "@/types/api/request";
import SelectProductDialog from "@/components/Product/SelectProductDialog.vue";
import {Getter} from "vuex-class";
import {ApiResponse, CommonResponse, Supplier, Warehouse} from "@/types/api";

@Component({
    components: {SelectProductDialog},
})
export default class NewOrderTab extends Vue {
  displaySelectProductDialog: boolean = false;
  productList = [];
  supplierList: ApiResponse<Supplier>[] = [];
  stockList: ApiResponse<Warehouse>[] = [];
  salesList = [];
  settleList = [];
  platformList = [];
  orderFormData : CreateOrderRequest = {...initialCreateOrder};

  mounted(): void {
    this.api.getSupplyList().then((res) => (this.supplierList = res));
    this.api.getWarehouseList().then((res) => (this.stockList = res));
    this.api.getSalesList().then((res) => (this.salesList = res));
    this.api.getPlatformList().then((res) => (this.platformList = res));
  }
  

  updateSelectedProducts(selected: any[]): void {
    this.productList = selected;
    this.displaySelectProductDialog = false;
  }

  deleteProduct(index) {
    this.productList.splice(index, 1);
  }

  displayDiscountPrice(price, amount):number {
    if( Number.isNaN(amount)) {
      amount = 0;
    }

    return price * amount / 100
  }

  displayTotalPrice(price, amount):number {
    if( Number.isNaN(amount)) {
      amount = 0;
    }

    return price * amount
  }


  getSummary({ columns, data }) {
    const sums = [];
    columns.forEach((columns, index) => {
      if (index === 0){
        sums[index] = "Sum";
      }
      else if (index === 6) {
        sums[index] = data.reduce((prev, curr) => {
          const result = prev + Number(curr.amount)
          return (
            Number.isNaN(result) ? 0 : result
          );
        }, 0);
      }
      else if (index === 10) {
        sums[index] = data.reduce((prev, curr) => {
          const result = prev + (curr.wholesale_price * curr.amount)
          return (
            Number.isNaN(result) ? 0 : result
          );
        }, 0);
      }
      else if (index === 11) {
        sums[index] = data.reduce((prev, curr) => {
          const result = prev + (curr.wholesale_price * curr.discount / 100 * curr.amount)
          return (
            Number.isNaN(result) ? 0 : result
          );
        }, 0);
      }
      else
      {
        sums[index] = "";
      }
    });
    return sums;
  }
  
  save(): void {
    this.orderFormData.place_time = formatDate(
      this.orderFormData.place_time,
      DateFormat.YMDDashed,
    );
    const specs = this.productList.map((product)=> {return {
      id: product.id,
      count: Number(product.amount)
    }})
    const params = {order: this.orderFormData, specs}
    this.api.addOrder(params).then(() => {
      this.resetForm()
    });
  }

  resetForm () {
    this.orderFormData = {...initialCreateOrder};
    this.productList = [];
  }
}
