








































































import ProductSpec from "@/components/Product/ProductInfo/ProductSpec.vue";
import ProductImage from "@/components/Product/ProductInfo/ProductImage.vue";
import ProductOthers from "@/components/Product/ProductInfo/ProductOthers.vue";
import ProductSelfDefinedAttr from "@/components/Product/ProductSelfDefinedAttr.vue";
import SelectUnit from "@/components/common/SelectUnit.vue";
import SelectCategory from "@/components/common/SelectCategory.vue";
import SelectBrand from "@/components/common/SelectBrand.vue";
import {Component, Mixins, Prop, Watch} from "vue-property-decorator";
import {AddProductRequest} from "@/types/api/request";
import AsModal from "@/mixins/AsModal";
import {initialAddProductRequest} from "@/core/initializer/initialAddProductRequest";

@Component({
  components: {
    SelectBrand,
    SelectCategory,
    SelectUnit,
    ProductImage,
    ProductSpec,
    ProductOthers,
    ProductSelfDefinedAttr,
  },
})
export default class EditProductDialog extends Mixins(AsModal) {
  @Prop(Number) readonly id!: number;

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.getData();
    } else {
      this.productFormData = {...initialAddProductRequest()};
    }
  }

  submit(): void {
    this.api.updateProduct(this.productFormData, this.id).then(() => {
      this.dismiss();
      this.refresh();
    });
  }

  getData(): void {
    this.api.getProduct(this.id).then((res) => {
      this.productFormData = {...res};
    });
  }

  productFormData: AddProductRequest = {...initialAddProductRequest()};
}
