import Vue from "vue";
import Router from "vue-router";
import Frame from "./views/Frame.vue";
import Login from "./views/Login.vue";
import Error from "./views/Error.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  // base: process.env.BASE_URL,
  base: "/erp",
  routes: [
    {
      path: "/",
      redirect: {
        name: "login",
      },
    },
    {
      path: "/frame",
      name: "frame",
      component: Frame,
    },
    {
      path: "/login",
      name: "login",
      component: Login,
    },
    {
      path: "*",
      name: "error",
      component: Error,
    },
  ],
});
