



































import {AppContext} from "@/core/api/AppContext";
import {PERMISSION_ACTION, PermissionActions} from "@/types/store/PermissionStore";
import {Component, Vue} from "vue-property-decorator";
import {Action} from "vuex-class";
import {LoginRequest} from "@/types/api";

@Component
export default class Login extends Vue {
  @Action(PERMISSION_ACTION.UPDATE_TOKEN)
  updateToken!: PermissionActions["UPDATE_TOKEN"];
  @Action(PERMISSION_ACTION.UPDATE_PERMISSION)
  updatePermission!: PermissionActions["UPDATE_PERMISSION"];

  formData: LoginRequest = {
    email: "admin@gmail.com",
    password: "123456",
  };

  version: string = "1.0.1.20200421";

  redirect(name: string): void {
    this.$router.push({name});
  }

  login(): void {
    this.doApiWithMessage(this.api.login(this.formData))
      .then((res) => {
        this.updateToken(res.token);
        AppContext.buildApiExecutor();
        this.api.getUserInfo().then((userInfo) => {
          const {id, name, email} = userInfo;
          this.updatePermission({id, name, email});
          this.redirect("frame");
        });
      })
      .catch(console.warn);
  }
}
