import {ApiExecutor} from "@/core/api/ApiExecutor";
import {AppContext} from "@/core/api/AppContext";
import Vue from "vue";
import {Component} from "vue-property-decorator";

export interface DoApiWithMessageOptions<T> {
  successMsg?: string;
  errorMsg?: string;
}

@Component
export default class WithApis extends Vue {
  get api(): ApiExecutor {
    return AppContext.ApiExecutor;
  }

  doApiWithMessage<T = void>(
    promise: Promise<T & {errorMessage?: string}>,
    options: DoApiWithMessageOptions<T> = {},
  ): Promise<T> {
    const {successMsg = "Success!", errorMsg} = options;
    return promise
      .then((response) => {
        this.$message({message: successMsg, type: "success"});
        return response;
      })
      .catch((response) => {
        const errorMessage = errorMsg ? errorMsg : response.message;
        this.$message({
          message: errorMessage,
          type: "error",
        });
        return Promise.reject(response);
      });
  }
}
