






















































import SelectBrand from "@/components/common/SelectBrand.vue";
import {Component, Mixins, Prop, Watch} from "vue-property-decorator";
import AsModal from "@/mixins/AsModal";
@Component({
  components: {SelectBrand},
})
export default class StockStatusDetailModal extends Mixins(AsModal) {
  @Prop(Object) readonly detail;
  itemList = [];
  warehouseList = [];

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.api.getWarehouseList().then((res) => (this.warehouseList = res));
      this.api.getStock(this.detail.id).then((res) => {
        this.itemList = res;
      });
    } else {
    }
    this.itemList = [];
  }

  displayWarehouseName(id) {
    const warehouse = this.warehouseList.find((warehouse) => warehouse.id === id);
    return warehouse.name;
  }
}
