














































import {Component, Emit, Mixins, Watch} from "vue-property-decorator";
import AsModal from "@/mixins/AsModal";
import {ProductCategoryTreeNode, ProductCategoryTreeNodes} from "@/types/ProductCategoryTreeNode";
import {buildProductCategory} from "@/core/builder/buildProductCategory";

@Component
export default class SelectOneProductDialog extends Mixins(AsModal) {
  readonly treeProps = {label: "name"};
  productCategoryData: ProductCategoryTreeNodes = [];
  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.getProductCategoryList();
      this.api.getProductSpecList().then((res) => (this.specList = res));
    }
  }

  onSelect(selected) {
    this.save(selected);
    this.dismiss();
  }

  @Emit()
  save(selected) {
    return selected;
  }

  getProductCategoryList(): void {
    this.api.getProductCategoryList().then((res) => {
      this.productCategoryData = buildProductCategory(res);
    });
  }

  specList = [];
  productFormData = {
    productId: "",
    productNumber: "",
    name: "",
    category: "",
    brand: "",
    unit: "",
    note: "",
    standardPrice: "",
    wholesalePrice: "",
    referencePrice: "",
    weight: "",
    volume: "",
    size: {length: "", width: "", height: ""},
    barcode: "",
  };

  handleNodeClick(node) {
    // if( node.level === 3) {
    //   this.cleanSearchFilter();
    //   this.productFormData.category = node.id;
    //   this.getProductList()
    // }
    // if( node.id === 0 ){
    //   this.cleanSearchFilter();
    //   this.getProductList()
    // }
  }
}
