


































import { Component, Vue } from 'vue-property-decorator'

@Component({
})
export default class GiftNote extends Vue {
  productFormData = {
    storeOut: '',
    storeIn: '',
  };

  resetForm () {
    this.$nextTick(() => {
      const ref: any = this.$refs['productFormRef']
      ref.resetFields()
    })
    this.productFormData = {
      storeOut: '',
      storeIn: '',
    }
  }
}
