import WithApis from "@/mixins/WithApis";
import Vue from "vue";
import {DateFormat} from "@/core/constants/DateFormat";
import {formatDate} from "@/core/utils/formatDate";
import VueLodash from "vue-lodash";

// modules
Vue.use(VueLodash);

// mixins
Vue.mixin(WithApis);

// filters
Vue.filter("formatDate", (value: string, format: DateFormat = DateFormat.YMD) => formatDate(value, format));
