  tabItem: Array<{title: string; name: string; component: string}> = [
    {
      title: "首頁",
      name: "Home",
      component: "Home",
    },
  ];

  addTab(targetObj: any) {
    if (targetObj.name === "") {
      return;
    }

    let tabExist = false;
    for (var item of this.tabItem) {
      if (item.name === targetObj.name) {
        tabExist = true;
        break;
      }
    }

    if (!tabExist) {
      this.tabItem.push({
        title: targetObj.title,
        name: targetObj.name,
        component: targetObj.component,
      });
    }

    this.currentTab = targetObj.name;
  }

  removeTab(targetName: any) {
    let tabs = this.tabItem;
    let activeName = this.currentTab;
    if (activeName === targetName) {
      tabs.forEach((tab, index) => {
        if (tab.name === targetName) {
          let nextTab = tabs[index + 1] || tabs[index - 1];
          if (nextTab) {
            activeName = nextTab.name;
          }
        }
      });
    }

    this.currentTab = activeName;
    this.tabItem = tabs.filter((tab) => tab.name !== targetName);
  }

  logout(): void {
    this.api
      .logout()
      .then(() => {
        this.clearToken();
        AppContext.buildApiExecutor();
        this.$router.push({name: "login"});
      })
      .catch(() => {
        this.clearToken();
        AppContext.buildApiExecutor();
        this.$router.push({name: "login"});
      });
  }
