










































































import AddPlatformDialog from "@/components/Setting/PlatformManagement/AddPlatformDialog.vue";
import {Component, Vue} from "vue-property-decorator";
import {CommonResponse} from "@/types/api";

@Component({
  components: {AddPlatformDialog},
})
export default class PlatformManagementTab extends Vue {
  displayAddPlatformDialog: boolean = false;
  productFormData = {
    name: "",
    spec: "",
    barcode: "",
    brand: "",
    status: "1",
  };
  productTableData = [
    {id: 40, storehouseId: "D0001", barcode: "barcode", name: "A", type: "Momo", productNumber: "CI9802-010"},
    {id: 21, storehouseId: "D0002", barcode: "barcode", name: "B", type: "Momo", productNumber: "CJ0315-010"},
    {id: 37, storehouseId: "D0003", barcode: "barcode", name: "C", type: "Momo", productNumber: "BV5131-363"},
    {id: 8, storehouseId: "D0004", barcode: "barcode", name: "D", type: "Momo", productNumber: "AT3099-010"},
  ];

  productTablePaginationData = {
    totalRows: 400,
    currentPage: 1,
    currentPageSize: 20,
    pageSizes: [20, 50, 100, 200],
  };

  platformList: CommonResponse = [];

  mounted(): void {
    this.getData();
  }

  getData(): void {
    this.api.getPlatformList().then((res) => {
      this.platformList = res;
    });
  }

  resetForm() {
    this.$nextTick(() => {
      const ref: any = this.$refs["productFormRef"];
      ref.resetFields();
    });
    this.productFormData = {
      name: "",
      spec: "",
      barcode: "",
      brand: "",
      status: "1",
    };
  }
}
