import {Component, Prop, Vue} from "vue-property-decorator";

@Component
export default class AsInputable extends Vue {
  @Prop(Boolean) readonly disabled!: boolean;
  @Prop(Boolean) readonly required!: boolean;
  @Prop(Boolean) readonly multiple!: boolean;
  @Prop(Boolean) readonly clearable!: boolean;
  @Prop({type: Boolean, default: true}) readonly withAll!: boolean;
  @Prop(String) readonly prop!: string;

  input(value: any): void {
    this.$emit("input", value);
    this.$emit("change", value);
  }
}
