import {EventEmitter} from "events";
import axios, {AxiosInstance, AxiosRequestConfig, AxiosResponse} from "axios";
import qs from "qs";
import uuidV4 from "uuid/v4";
import {ApiCallState} from "./ApiCallState";
import {ApiConfig} from "./ApiConfig";

function buildRequestContent(axiosRequestConfig: AxiosRequestConfig): string {
  const {method, url, data} = axiosRequestConfig;
  return `${method} ${url} \n=== request === \n${JSON.stringify(data, null, 2)}`;
}

function buildResponseContent(axiosResponse: AxiosResponse): string {
  const {status, data} = axiosResponse;
  return `=== response (status = ${status}) ===\n${JSON.stringify(data, null, 2)}`;
}

export enum EVENT_API {
  CALL = "onApiStatus",
  CALL_CHANGED = "onCallingApisChanged",
  CALL_STARTED = "onApiCallStarted",
  CALL_ENDED = "onApiCallEnded",
  CALL_ERROR = "onApiCallError",
  CALL_TIMEOUT = "onApiCallTimeout",
}

export class ApiCaller {
  private readonly axiosInstance: AxiosInstance;
  private readonly callingApisEventEmitter = new EventEmitter();
  private readonly responseStatusEventEmitter = new EventEmitter();
  private callingApis: {[index: string]: ApiCallState[]} = {};

  // eslint-disable-next-line @typescript-eslint/no-parameter-properties
  constructor(private readonly config: ApiConfig) {
    const {baseURL, timeout = 0} = config;
    this.axiosInstance = axios.create({
      baseURL,
      timeout,
    });
    this.setUpEvents();
    this.setUpInterceptors();
  }

  public async get<T, S>(url: string, payload?: T): Promise<S> {
    const paramsUrl = payload && Object.keys(payload).length > 0 ? "?" + qs.stringify(payload, {indices: false}) : "";
    return await this.executeRequest<S>(url, async () => (await this.axiosInstance.get<S>(`${url}${paramsUrl}`)).data);
  }

  public async post<T, S>(url: string, payload: T): Promise<S> {
    return await this.executeRequest<S>(url, async () => (await this.axiosInstance.post<S>(`${url}`, payload)).data);
  }

  public async patch<T, S>(url: string, payload: T): Promise<S> {
    return await this.executeRequest<S>(url, async () => (await this.axiosInstance.patch<S>(`${url}`, payload)).data);
  }

  public async put<T, S>(url: string, payload: T): Promise<S> {
    return await this.executeRequest<S>(url, async () => (await this.axiosInstance.put<S>(`${url}`, payload)).data);
  }

  public async delete<T, S>(url: string, payload: T): Promise<void> {
    const paramsUrl = Object.keys(payload).length === 0 ? "" : "?" + qs.stringify(payload, {indices: false});
    await this.executeRequest(url, () => this.axiosInstance.delete(`${url}${paramsUrl}`));
  }

  public get axios(): AxiosInstance {
    return this.axiosInstance;
  }

  private async executeRequest<S>(url: string, requestFn: () => Promise<S>): Promise<S> {
    const txId = uuidV4();
    try {
      this.callingApisEventEmitter.emit(EVENT_API.CALL_STARTED, url, txId);
      const result = await requestFn();
      this.callingApisEventEmitter.emit(EVENT_API.CALL_ENDED, url, txId);
      return result;
    } catch (e) {
      if (e.code === "ECONNABORTED") {
        this.callingApisEventEmitter.emit(EVENT_API.CALL_TIMEOUT, url, txId, e);
      } else {
        this.callingApisEventEmitter.emit(EVENT_API.CALL_ERROR, url, txId, e);
      }
      throw e;
    }
  }

  private setUpEvents(): void {
    const {onCallingApiStateChanged} = this.config;

    if (onCallingApiStateChanged) {
      this.callingApisEventEmitter.on(EVENT_API.CALL_CHANGED, () => {
        onCallingApiStateChanged(this.callingApis);
      });
      this.callingApisEventEmitter.on(EVENT_API.CALL_STARTED, (url: string, txId: string) => {
        const callingApiStates = this.callingApis[url] || [];
        callingApiStates.push({
          txId,
        });
        this.callingApis[url] = callingApiStates;
        this.callingApisEventEmitter.emit(EVENT_API.CALL_CHANGED);
      });
      this.callingApisEventEmitter.on(EVENT_API.CALL_ENDED, (url: string, txId: string) => {
        const callingApiStates = this.callingApis[url];
        this.callingApis[url] = callingApiStates.filter((it) => it.txId !== txId);
        onCallingApiStateChanged(this.callingApis);
      });
      this.callingApisEventEmitter.on(EVENT_API.CALL_ERROR, (url: string, txId: string, error: any) => {
        const callingApiStates = this.callingApis[url];
        callingApiStates
          .filter((it) => it.txId === txId)
          .forEach((it) => {
            it.error = error;
          });
        onCallingApiStateChanged(this.callingApis);
      });
      this.callingApisEventEmitter.on(EVENT_API.CALL_TIMEOUT, (url: string, txId: string) => {
        const callingApiStates = this.callingApis[url];
        callingApiStates
          .filter((it) => it.txId === txId)
          .forEach((it) => {
            it.error = "timeout";
          });
        onCallingApiStateChanged(this.callingApis);
      });
    }

    this.responseStatusEventEmitter.on(EVENT_API.CALL, (response: AxiosResponse) => {
      if (response.status >= 400 && this.config.errorStatusHandler) {
        this.config.errorStatusHandler(response.status, response.data);
      }
    });
  }

  private setUpInterceptors(): void {
    const {debug, tokenProvider, extraHeaderProvider} = this.config;

    this.axiosInstance.interceptors.request.use(async (requestConfig: AxiosRequestConfig) => {
      try {
        const token = await tokenProvider(requestConfig.url as string);
        if (token) {
          requestConfig.headers.Authorization = token;
        }
      } catch (e) {
        // ignoring when token failed to retrieve token.
      }

      if (extraHeaderProvider) {
        const extraHeaders = await extraHeaderProvider(requestConfig.url as string);
        Object.keys(extraHeaders).forEach((headerKey) => {
          requestConfig.headers[headerKey] = extraHeaders[headerKey];
        });
      }

      return requestConfig;
    });

    this.axiosInstance.interceptors.response.use(
      (response: AxiosResponse) => {
        if (debug) {
          console.log(`${buildRequestContent(response.config)}${buildResponseContent(response)}`);
        }

        this.handleResponseStatus(response);

        return response;
      },
      (err) => {
        const config: AxiosRequestConfig = err.config;
        const response: AxiosResponse = err.response;
        if (debug) {
          console.log(`${config && buildRequestContent(config)}${response && buildResponseContent(response)}`);
        }

        this.handleResponseStatus(response);

        return Promise.reject(err);
      },
    );
  }

  private handleResponseStatus(response: AxiosResponse): void {
    if (response && response.status) {
      this.responseStatusEventEmitter.emit(EVENT_API.CALL, response);
    }
  }
}
