import Store from "store";

export class PermissionStorage {
    private static namespace = "permission";

    public static set<T>(name: string, value: T): void {
        Store.set(PermissionStorage.getNamespaced(name), value);
    }

    public static get<T>(name: string, defaultValue?: T): T {
        return Store.get(PermissionStorage.getNamespaced(name), defaultValue);
    }

    public static clear(): void {
        Store.clearAll();
    }

    private static getNamespaced(name: string): string {
        return `${PermissionStorage.namespace}.${name}`;
    }
}
