













import AsInputable from "@/mixins/AsInputable";
import {Component, Mixins, Prop} from "vue-property-decorator";
import {CommonResponse} from "@/types/api";

@Component
export default class SelectWarehouse extends Mixins(AsInputable) {
  @Prop(Number) readonly value!: string;
  @Prop({type: String, default: "倉庫"}) readonly label!: string;

  warehouseList: CommonResponse = [];

  beforeMount(): void {
    this.api.getWarehouseList().then((res) => (this.warehouseList = res));
  }

  get warehouseOptions(): CommonResponse {
    const all: CommonResponse = [{name: "全部", id: null}];
    return this.withAll ? all.concat(this.warehouseList) : this.warehouseList;
  }
}
