




















































































import {Component, Vue} from "vue-property-decorator";

@Component({})
export default class ExpressBillManagementTab extends Vue {
  editProductId: number = 0;
  productBrandList = [];
  productFormData = {
    name: "",
    spec: "",
    barcode: "",
    brand: null,
    status: "Y",
    applicationTime: null
  };
  productTableData = [];
  productTablePaginationData = {
    totalRows: 400,
    currentPage: 1,
    currentPageSize: 20,
    pageSizes: [20, 50, 100, 200],
  };

  mounted(): void {
  }

  resetForm() {
    this.$nextTick(() => {
      const ref: any = this.$refs["productFormRef"];
      ref.resetFields();
    });
    this.productFormData = {
      name: "",
      spec: "",
      barcode: "",
      brand: null,
      status: "Y",
      applicationTime: null
    };
  }
}
