import config from "@/config";
import {ApiExecutor} from "@/core/api/ApiExecutor";
import {tokenProvider} from "@/core/api/tokenProvider";

let apiExecutor: ApiExecutor;

export class AppContext {
  public static get ApiExecutor(): ApiExecutor {
    if (!apiExecutor) {
      this.buildApiExecutor();
    }

    return apiExecutor;
  }

  public static buildApiExecutor(): void {
    apiExecutor = new ApiExecutor({
      baseURL: config.API_URL,
      tokenProvider,
      debug: config.apiDebug,
    });
  }
}
