




















































































































































































































import {Component, Vue} from "vue-property-decorator";
import {formatTimeRange} from "@/core/utils/formatTimeRange";
import {GetOrderListResponse} from "@/types/api/response";
import {ApiResponse, Supplier} from "@/types/api";
import SelectWarehouse from "@/components/common/SelectWarehouse.vue";
import SelectPlatform from "@/components/common/SelectPlatform.vue";

@Component({
  components: {SelectPlatform, SelectWarehouse},
})
export default class InquiryOrderTab extends Vue {
  supplierList: ApiResponse<Supplier>[] = [];
  orderDetail = null;
  salesList = [];
  productList = [];
  settleList = [];
  platformList = [];
  searchDate = null;

  mounted(): void {
    this.api.getProductList().then((res) => (this.productList = res));
    this.api.getSupplyList().then((res) => (this.supplierList = res));
    this.api.getSalesList().then((res) => (this.salesList = res));
    this.getData();
  }

  getData(): void {
    const searchDate = formatTimeRange(this.searchDate);
    const timeParams = {gte: searchDate[0], lte: searchDate[1]};
    const params = {...this.orderFormData, ...timeParams};
    this.api.getOrderList(params).then((res) => {
      this.orderTableData = res;
    });
  }

  orderTableData: GetOrderListResponse = [];

  orderFormData = {
    brand: null,
    code: null,
    saleman_id: null,
    number: null,
    product: null,
    warehouse_id: null,
    platform_id: null,
    print: null,
    logistics: null,
  };

  purchaseTablePaginationData = {
    totalRows: Object.keys(this.orderTableData).length,
    currentPage: 1,
    currentPageSize: 20,
    pageSizes: [20, 50, 100, 200],
  };

  getOrderProductSpecs(row) {
    this.api.getOrderProductSpecs(row.id).then((res) => {
      this.orderDetail = res;
    });
  }

  reset() {
    this.orderFormData = {
      brand: null,
      code: null,
      saleman_id: null,
      number: null,
      product: null,
      warehouse_id: null,
      platform_id: null,
      print: null,
      logistics: null,
    };
  }

  displayStatus(status: number): string {
    // TODO: refactor this
    let statusLabel = "";

    if (status === -1) {
      statusLabel = "關閉";
    } else if (status === 0) {
      statusLabel = "審核中";
    } else if (status === 1) {
      statusLabel = "待出貨";
    } else if (status === 2) {
      statusLabel = "出貨";
    } else if (status === 3) {
      statusLabel = "異常";
    } else {
      return statusLabel;
    }

    return statusLabel;
  }
}
