








import AsInputable from "@/mixins/AsInputable";
import {Component, Mixins, Prop} from "vue-property-decorator";
import {CommonResponse} from "@/types/api";

@Component
export default class SelectSupplier extends Mixins(AsInputable) {
  @Prop(Number) readonly value!: string;
  @Prop({type: String, default: "結算方式"}) readonly label!: string;

  settleList: CommonResponse = [];

  beforeMount(): void {
    this.api.getPurchaseSettleList().then((res) => (this.settleList = res));
  }

  get settleOptions(): CommonResponse {
    const all: CommonResponse = [{name: "全部", id: null}];
    return this.withAll ? all.concat(this.settleList) : this.settleList;
  }
}
