






























import { Component, Emit, Prop, Vue } from 'vue-property-decorator'

@Component
export default class AddImageDialog extends Vue {
  @Prop(Boolean) readonly visible!: boolean;
  @Emit('dismiss') dismiss (): void {}
  @Emit('update:visible') close (): boolean {
    return false
  }

  imageFormData = {
    imageUrl: ''
  };

  resetForm () {
    this.$nextTick(() => {
      const ref: any = this.$refs['ImageFormRef']
      ref.resetFields()
    })
    this.imageFormData = {
      imageUrl: ''
    }
  }
}
