













import AsInputable from "@/mixins/AsInputable";
import {Component, Mixins, Prop} from "vue-property-decorator";
import {CommonResponse, ProductCategory, ApiResponse} from "@/types/api";

@Component
export default class SelectCategory extends Mixins(AsInputable) {
  @Prop(Number) readonly value!: string;
  @Prop({type: String, default: "分類"}) readonly label!: string;

  categoryList: ApiResponse<ProductCategory>[] = [];

  beforeMount(): void {
    this.api
      .getProductCategoryList()
      .then((res) => (this.categoryList = res.filter((category) => category.level === 3)));
  }

  get categoryOptions(): CommonResponse {
    const all: CommonResponse = [{name: "全部", id: null}];
    return this.withAll ? all.concat(this.categoryList) : this.categoryList;
  }
}
