


























































































































import {Component, Vue} from "vue-property-decorator";
import * as Papa from "papaparse";
import {parseImportOrder, translateOrderToParams} from "@/core/utils/parseImportOrder";
import SelectPlatform from "@/components/common/SelectPlatform.vue";
@Component({
  components: {SelectPlatform},
})
export default class ImportOrderTab extends Vue {
  uploadSuccess: boolean = false;

  get canImport() {
    return this.uploadSuccess && !!this.selectedPlatformId && this.orderList.length;
  }

  orderList = [];
  orderSpecList = [];
  selectedPlatformId = null;
  tt = {};

  upload(options: {file: File}) {
    console.log(options);
    Papa.parse(options.file, {
      complete: (results) => {
        const header = results.data[0];
        const body = results.data.slice(1);
        this.orderList = parseImportOrder(header, body);

        if (this.orderList.length) {
          this.uploadSuccess = true;
        } else {
          this.$message({message: "匯入失敗，請確認欄位是否正確。", type: "error"});
        }
      },
    });
  }

  getDetail(data) {
    this.orderSpecList = data.specs;
  }

  onImport() {
    const params = translateOrderToParams(this.orderList, this.selectedPlatformId);
    this.api
      .addMultiOrder(params)
      .then(() => {
        this.$message({message: "匯入成功", type: "success"});
        this.resetImport();
      })
      .catch(() => {
        this.$message({message: "匯入失敗，請確認是否建立商品對應關係。", type: "error"});
        this.resetImport();
      });
  }

  resetImport() {
    this.orderList = [];
    this.uploadSuccess = false;
  }

  deleteOrder(index) {
    this.orderList.splice(index, 1);
  }
}
