






















































































































































import AddStockTransferDialog from '@/components/Stock/StockTransfer/AddStockTransferDialog.vue'
import EditStockTransferDialog from '@/components/Stock/StockTransfer/EditStockTransferDialog.vue'
import StockTransferDetail from "@/components/Stock/StockTransfer/StockTransferDetail.vue";
import {paramsToQuery} from "@/core/utils/paramsToQuery";
import {formatTimeRange} from "@/core/utils/formatTimeRange";
import {Component, Vue} from 'vue-property-decorator'
import {GetStockTransferListResponse} from "@/types/api/response";
import {ApiResponse, Warehouse} from "@/types/api";

@Component({
  components: { AddStockTransferDialog, StockTransferDetail, EditStockTransferDialog }
})
export default class StockTransferTab extends Vue {
  displayAddStockTransferDialog: boolean = false;
  displayStockTransferDetailDialog: boolean = false;
  displayEditStockTransferDialog: boolean = false;
  warehouseList: ApiResponse<Warehouse>[] = [];
  stockTransferDetail = null;
  editStockTransfer = null;
  stockTransferList = [];
  searchDate = null;

  

  mounted(): void {
    this.api.getWarehouseList().then((res) => (this.warehouseList = res));
    this.getData();
  }

  getData(): void {
    const searchDate = formatTimeRange(this.searchDate);
    const timeParams = {gte: searchDate[0], lte: searchDate[1]};
    const params = {...this.stockTransferFormData, ...timeParams};
    this.api.getStockTransferList(params).then((res) => {
      this.stockTransferTableData = res;
    });
  }

  getDetail(id: number): void {
    this.api.getStockTransfer(id).then((res) => {
      this.stockTransferDetail = res;
      this.displayStockTransferDetailDialog = true;
    });
  }

  editDetail(id: number): void {
    this.api.getStockTransfer(id).then((res) => {
      this.stockTransferDetail = res;
      this.displayEditStockTransferDialog = true;
    });
  }
  
  stockTransferTableData: GetStockTransferListResponse = [];
  stockTransferFormData = {
    business_date: null,
    bring_in_warehouse_id: null,
    bring_out_warehouse_id: null,
    category_name: null,
    number: null,
    state: null,
    product: null,
  }
  

  deleteTransfer(id: number): void {
    this.api.deleteStockTransfer(id).then(() => this.getData());
  }


  stockTransferTablePaginationData = {
    totalRows: 400,
    currentPage: 1,
    currentPageSize: 20,
    pageSizes: [20, 50, 100, 200]
  }


  resetForm () {
    this.stockTransferFormData = {
      business_date: null,
      bring_in_warehouse_id: null,
      bring_out_warehouse_id: null,
      category_name: null,
      number: null,
      state: null,
      product: null,
    }
  }
}
