





































import {Component, Emit, Prop, Vue, Watch} from "vue-property-decorator";
import PurchaseInSpecsDetail from "@/components/Purchase/PurchaseOrder/PurchaseInSpecsDetail.vue";

@Component({
  components: {PurchaseInSpecsDetail},
})
export default class SelectPurchaseDialog extends Vue {
  displayPurchaseDetailDialog: boolean = false;
  @Prop(Boolean) readonly visible!: boolean;
  @Prop(Array) readonly selected;
  @Emit("dismiss") dismiss(): void {}
  @Emit("update:visible") close(): boolean {
    return false;
  }

  @Emit("update-selected")
  update() {
    return this.selectedPurchaseList;
  }
  purchaseTableData = [];
  specTable = [];
  selectedPurchaseList = [];
  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.getData();
    } else {
      this.selectedPurchaseList = [];
    }
  }

  purchaseDetail = null;
  getData(): void {
    this.api.getPurchaseList({status: 0}).then((res) => {
      this.purchaseTableData = res;
    });
  }

  getDetail(id: number): void {
    this.api.getPurchaseSpecs(String(id)).then((res) => {
      this.purchaseDetail = res;
      this.displayPurchaseDetailDialog = true;
    });
  }

  onSelectionChange(selected: any[]) {
    this.selectedPurchaseList = selected.map((select) => {
      return {
        id: select.id,
        code: select.code,
      };
    });
  }

  save() {
    this.update();
    this.dismiss();
  }
}
