

























































































































import { Component, Vue } from 'vue-property-decorator'

@Component({})
export default class ProductSelfDefinedSpecTab extends Vue {
  currentSpecId = 1
  specTableData = [
    { id: 1, name: 'color', specNum: [
        { id: 10, number: '201', value: 'white' },
        { id: 11, number: '202', value: 'white' },
      ]
    },
    { id: 2, name: 'others', specNum: [] }
  ];
  extendedSpecTableData = [
    { id: 1, name: 'water proof' }
  ];
  specFormData = {
    number: null,
    value: ""
  };

  addExtendedSpec (scope: any) {
    this.extendedSpecTableData.push({id: this.extendedSpecTableData.length+1, name: ''})
  }
  addSpecNumber (scope: any) {
    for (var index in this.specTableData) {
      if (this.specTableData[index].id === this.currentSpecId) {
        this.specTableData[index].specNum.push({id: null, number: '', value: ''})
        break;
      }
    }
  }
  handleCurrentChange (val: any) {
    this.currentSpecId = val.id
  }
  getSpecNumberTableData () {
      for (var val of this.specTableData) {
          if (val.id === this.currentSpecId) {
              return val.specNum
          }
      }
      return []
  }
}
