

























































import {Component, Mixins, Prop, Watch} from "vue-property-decorator";
import AsModal from "@/mixins/AsModal";
import {initialWarehouse} from "@/core/initializer/initialWarehouse";
import {Warehouse} from "@/types/api";

@Component
export default class EditStorehouseDialog extends Mixins(AsModal) {
  @Prop({type: Number, default: 0}) readonly id!: number;

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val && this.id !== 0) {
      this.api.getWarehouse(this.id).then((res) => {
        this.formData = {...this._.omit(res, ["id", "created_at", "updated_at"])};
      });
    } else {
      this.refresh();
      this.formData = {...initialWarehouse()};
    }
  }

  formData: Warehouse = {...initialWarehouse()};

  save(): void {
    this.api.editWarehouse(this.formData, this.id).then(() => {
      this.formData = {...initialWarehouse()};
      this.refresh();
      this.dismiss();
    });
  }
}
