








import AsInputable from "@/mixins/AsInputable";
import {Component, Mixins, Prop} from "vue-property-decorator";
import {CommonResponse} from "@/types/api";

@Component
export default class SelectUnit extends Mixins(AsInputable) {
  @Prop(Number) readonly value!: string;
  @Prop({type: String, default: "單位"}) readonly label!: string;

  unitList: CommonResponse = [];

  beforeMount(): void {
    this.api.getProductUnitList().then((res) => (this.unitList = res));
  }

  get unitOptions(): CommonResponse {
    const all: CommonResponse = [{name: "全部", id: null}];
    return this.withAll ? all.concat(this.unitList) : this.unitList;
  }
}
