








import AsInputable from "@/mixins/AsInputable";
import {Component, Mixins, Prop} from "vue-property-decorator";
import {CommonResponse} from "@/types/api";

@Component
export default class SelectSales extends Mixins(AsInputable) {
  @Prop(Number) readonly value!: string;
  @Prop({type: String, default: "業務員"}) readonly label!: string;

  salesList: CommonResponse = [];

  beforeMount(): void {
    this.api.getSalesList().then((res) => (this.salesList = res));
  }

  get salesOptions(): CommonResponse {
    const all: CommonResponse = [{name: "全部", id: null}];
    return this.withAll ? all.concat(this.salesList) : this.salesList;
  }
}
