






































































import AddStorehouseDialog from "@/components/Basic/Storehouse/AddStorehouseDialog.vue";
import {Component, Vue} from "vue-property-decorator";
import EditStorehouseDialog from "@/components/Basic/Storehouse/EditStorehouseDialog.vue";
import {ApiResponse, Warehouse} from "@/types/api";

@Component({
  components: {EditStorehouseDialog, AddStorehouseDialog},
})
export default class StorehouseTab extends Vue {
  displayAddStorehouseDialog: boolean = false;
  displayEditStorehouseDialog: boolean = false;
  warehouseList: ApiResponse<Warehouse>[] = [];
  selectedId: number = 0;

  mounted(): void {
    this.getData();
  }

  getData(): void {
    this.selectedId = 0;
    this.api.getWarehouseList().then((res) => {
      this.warehouseList = res;
    });
  }

  copyWarehouse(id: number): void {
    this.selectedId = id;
    this.displayAddStorehouseDialog = true;
  }

  editWarehouse(id: number): void {
    this.selectedId = id;
    this.displayEditStorehouseDialog = true;
  }

  productFormData = {
    name: "",
    spec: "",
    barcode: "",
    brand: "",
    status: "1",
  };

  resetForm() {
    this.$nextTick(() => {
      const ref: any = this.$refs["productFormRef"];
      ref.resetFields();
    });
    this.productFormData = {
      name: "",
      spec: "",
      barcode: "",
      brand: "",
      status: "1",
    };
  }
}
