















































































import AddAttributeDialog from "@/components/Product/BrandAndAttribute/AddAttributeDialog.vue";
import AddAttributeValueDialog from "@/components/Product/BrandAndAttribute/AddAttributeValueDialog.vue";
import AddBrandDialog from "@/components/Product/BrandAndAttribute/AddBrandDialog.vue";
import {Component, Vue} from "vue-property-decorator";
import {CommonResponse} from "@/types/api";

@Component({
  components: {AddBrandDialog, AddAttributeDialog, AddAttributeValueDialog},
})
export default class BrandAndAttributeTab extends Vue {
  brandList: CommonResponse = [];
  displayAddBrandDialog: boolean = false;

  mounted(): void {
    this.getBrandList();
  }

  addBrand(): void {
    this.$prompt("品牌名稱", "新增品牌", {
      confirmButtonText: "確定",
      cancelButtonText: "取消",
    })
      .then((action: any) => {
        this.api.addBrand(action.value).then(() => this.getBrandList());
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "取消!",
        });
      });
  }

  deleteBrand(id: number): void {
    this.api.deleteBrand(id).then(() => this.getBrandList());
  }

  getBrandList(): void {
    this.api.getProductBrandList().then((res) => (this.brandList = res));
  }

  displayAddAttributeDialog: boolean = false;
  displayAddAttributeValueDialog: boolean = false;
  disableCreateAttrValueButton: boolean = true;
  attributeTableData = [
    {
      id: 40,
      name: "color",
      value: "",
      children: [{id: 41, name: "", value: "white"}, {id: 42, name: "", value: "blue"}],
    },
    {id: 21, name: "size", value: "", children: []},
  ];

  handleCurrentChange(scope: any) {
    this.disableCreateAttrValueButton = !scope.children;
  }
}
