























































































































import {Component, Mixins, Watch} from "vue-property-decorator";
import {formatDate} from "@/core/utils/formatDate";
import {DateFormat} from "@/core/constants/DateFormat";
import AsModal from "@/mixins/AsModal";
import {initialCreatePurchase} from "@/core/initializer/initialCreatePurchase";
import {CreatePurchaseRequest} from "@/types/api/request";
import {Getter} from "vuex-class";
import SelectProductDialog from "@/components/Product/SelectProductDialog.vue";
import SelectPurchaseDialog from "@/components/Purchase/SelectPurchaseDialog.vue";
import {ApiResponse, CommonResponse, Supplier} from "@/types/api";

@Component({
  components: {SelectProductDialog, SelectPurchaseDialog},
})
export default class AddReviewInDialog extends Mixins(AsModal) {
  formData = {...this.initFormData()};
  displaySelectProductDialog: boolean = false;
  displaySelectPurchaseDialog: boolean = false;
  @Getter("permission/id") readonly id!: number;
  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.api.getWarehouseList().then((res) => (this.stockList = res));
      this.api.getSupplyList().then((res) => (this.supplyList = res));
      this.api.getSalesList().then((res) => (this.salesList = res));
      this.api.getPurchaseSettleList().then((res) => (this.settleList = res));
      this.formData = {...this.initFormData()};
    } else {
      this.specList = [];
    }
  }

  stockList = [];
  supplyList: ApiResponse<Supplier>[] = [];
  salesList: CommonResponse = [];
  settleList: CommonResponse = [];
  specList = [];

  save(): void {
    this.formData.business_date = formatDate(this.formData.business_date, DateFormat.YMDDashed);
    this.formData.specs = this.specList.map((spec) => {
      return {
        purchase_id: spec.purchase_id,
        id: spec.spec_id,
        count: spec.count,
      };
    });
    this.formData.ids = this._.uniq(
      this.specList.map((spec) => {
        return spec.purchase_id;
      }),
    );

    this.api.addPurchaseConfirm(this.formData).then(() => {
      this.refresh();
      this.dismiss();
    });
  }

  initFormData() {
    return {
      warehouse_id: null,
      remark: "",
      supply_id: null,
      business_date: "",
      saleman_id: null,
      ids: [],
      specs: [],
    };
  }

  updateSelected(selected) {
    this.specList = [];
    const selectedIds = selected
      .map((select) => {
        return select.id;
      })
      .join(",");
    this.api.getPurchaseSpecs(selectedIds).then((res) => {
      this.specList.push(...res);
    });
  }

  deleteSpec(index) {
    this.specList.splice(index, 1);
  }
}
