









































































































import SelectProductDialog from "@/components/Product/SelectProductDialog.vue";
import AddSellStockOutDialog from "@/components/Sell/SellStockOut/AddSellStockOutDialog.vue";

import {Component, Vue} from "vue-property-decorator";
import SelectSales from "@/components/common/SelectSales.vue";
import SelectWarehouse from "@/components/common/SelectWarehouse.vue";
import SelectPlatform from "@/components/common/SelectPlatform.vue";
import SellStockDetailModal from "@/components/order/SellStockDetailModal.vue";

@Component({
  components: {
    SellStockDetailModal,
    SelectPlatform,
    SelectWarehouse,
    SelectSales,
    SelectProductDialog,
    AddSellStockOutDialog,
  },
})
export default class SellStockOutTab extends Vue {
  displaySelectProductDialog: boolean = false;
  displayAddSellStockOutDialog: boolean = false;
  displaySellStockModal: boolean = false;
  stockOutFormData = {
    number: "",
  };
  orderList = [];
  orderDetail = null;

  getData() {
    this.api.getWarehouseOutOrders().then((res) => {
      this.orderList = res;
    });
  }

  getDetail(id) {
    this.api.getWarehouseOutOrder(id).then((res) => {
      this.orderDetail = res;
      this.displaySellStockModal = true;
    });
  }

  mounted() {
    this.getData();
  }
}
