














































import {Component, Mixins, Watch} from "vue-property-decorator";
import AsModal from "@/mixins/AsModal";
import {ProductSpec} from "@/types/api";
import {initialProductSpec} from "@/core/initializer/initialProductSpec";
import {PRODUCT_SPEC_ACTION, ProductSpecActions} from "@/types/store/ProductSpec";
import {Action} from "vuex-class";

@Component({})
export default class AddProductSpecDialog extends Mixins(AsModal) {
  @Action(PRODUCT_SPEC_ACTION.ADD_PRODUCT_SPEC)
  addProductSpec!: ProductSpecActions["ADD_PRODUCT_SPEC"];

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.specData = {...initialProductSpec()};
    }
  }

  save(): void {
    this.addProductSpec(this.specData);
    this.dismiss();
  }

  specData: ProductSpec = {...initialProductSpec()};
}
