






















































































































































import AddProductDialog from "@/components/Product/ProductInfo/AddProductDialog.vue";
import EditProductDialog from "@/components/Product/ProductInfo/EditProductDialog.vue";
import AddProductCategoryDialog from "@/components/Product/ProductInfo/AddProductCategoryDialog.vue";
import EditProductCategoryDialog from "@/components/Product/ProductInfo/EditProductCategoryDialog.vue";
import ProductDetail from "@/components/Product/ProductInfo/ProductDetail.vue";
import SelectBrand from "@/components/common/SelectBrand.vue";
import {Component, Vue} from "vue-property-decorator";
import {buildProductCategory} from "@/core/builder/buildProductCategory";
import {ProductCategoryTreeNode, ProductCategoryTreeNodes} from "@/types/ProductCategoryTreeNode";
import {GetProductListResponse} from "@/types/api/response";
import {GetProductListRequest} from "@/types/api/request/GetProductListRequest";
import {AddProductRequest} from "@/types/api/request";
import {initialAddProductRequest} from "@/core/initializer/initialAddProductRequest";

@Component({
  components: {
    SelectBrand,
    EditProductDialog,
    AddProductDialog,
    AddProductCategoryDialog,
    EditProductCategoryDialog,
    ProductDetail,
  },
})
export default class ProductInfoTab extends Vue {
  readonly treeProps = {label: "name"};

  displayAddProductDialog: boolean = false;
  displayDetailProductDialog: boolean = false;
  displayEditProductDialog: boolean = false;
  displayAddProductCategoryDialog: boolean = false;
  displayEditProductCategoryDialog: boolean = false;
  editProductId: number = 0;
  copyProductData: AddProductRequest = {...initialAddProductRequest()};
  productCategoryTreeCurrentNode: ProductCategoryTreeNode = {id: 0, name: "所有系統分類", children: []};
  productCategoryTreeCurrentNodeId: number = 0;
  productCategoryData: ProductCategoryTreeNodes = [];
  productFormData: GetProductListRequest = {
    name: "",
    spec: "",
    barcode: "",
    brand: null,
    status: "",
    category: null,
  };
  productTableData: GetProductListResponse = [];
  selectedProduct: GetProductListResponse = [];
  productDetail = null;

  afterProductAction(): void {
    this.getProductList();
    this.editProductId = 0;
    this.copyProductData = {...initialAddProductRequest()};
  }

  handleSelectionChange(selectedProduct: GetProductListResponse): void {
    this.selectedProduct = selectedProduct;
  }

  mounted(): void {
    this.getProductList();
    this.getProductCategoryList();
  }

  copyProduct(id: number): void {
    this.api.getProduct(id).then((res) => {
      this.copyProductData = {product: {...res}, specs: []};
      this.displayAddProductDialog = true;
    });
  }

  getDetail(id: number): void {
    this.api.getProduct(id).then((res) => {
      this.productDetail = res;
      this.displayDetailProductDialog = true;
    });
  }

  editProduct(id: number): void {
    this.editProductId = id;
    this.displayEditProductDialog = true;
  }

  getProductList(): void {
    this.api.getProductList(this.productFormData).then((res) => (this.productTableData = res));
  }

  enableProduct(): void {
    this.selectedProduct.forEach((product) => {
      this.api
        .updateProduct({status: "Y"}, product.id)
        .then(() => {
          this.selectedProduct = [];
          this.getProductList();
        })
        .catch(console.error);
    });
  }

  disableProduct(): void {
    this.selectedProduct.forEach((product) => {
      this.api
        .updateProduct({status: "N"}, product.id)
        .then(() => {
          this.selectedProduct = [];
          this.getProductList();
        })
        .catch(console.error);
    });
  }

  getProductCategoryList(): void {
    this.api.getProductCategoryList().then((res) => {
      this.productCategoryData = buildProductCategory(res);
    });
  }

  addProductCategory(): void {
    this.displayAddProductCategoryDialog = true;
  }

  handleCurrentChange(node: any) {
    this.productCategoryTreeCurrentNode = node;
    this.productCategoryTreeCurrentNodeId = node.id;
  }

  deleteProductCategory() {
    if (this.productCategoryTreeCurrentNode.children.length > 0) {
      this.$alert("當前選中類目含有子分類，請先刪除子分類！", "操作錯誤", {
        cancelButtonText: "確定",
        type: "error",
      });
    } else {
      this.$confirm("確認要刪除此分類嗎？ 刪除後，此分類下的商品將會變為未分類商品！", "提示", {
        confirmButtonText: "確定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.api
            .deleteProductCategoryList(this.productCategoryTreeCurrentNodeId)
            .then(() => this.getProductCategoryList());
        })
        .catch(() => {});
    }
  }

  handleNodeClick(node) {
    if (node.level === 3) {
      this.cleanSearchFilter();
      this.productFormData.category = node.id;
      this.getProductList();
    }
    if (node.id === 0) {
      this.cleanSearchFilter();
      this.getProductList();
    }
  }

  cleanSearchFilter() {
    this.productFormData = {
      name: "",
      spec: "",
      barcode: "",
      brand: null,
      status: "",
      category: null,
    };
  }

  resetSearchFilter() {
    this.cleanSearchFilter();
    this.getProductList();
  }

  onClickAdd() {
    this.copyProductData = {...initialAddProductRequest()};
    this.displayAddProductDialog = true;
  }
}
