












































































































import {Component, Mixins, Watch} from "vue-property-decorator";
import {formatDate} from "@/core/utils/formatDate";
import {DateFormat} from "@/core/constants/DateFormat";
import AsModal from "@/mixins/AsModal";
import {initialCreatePurchase} from "@/core/initializer/initialCreatePurchase";
import {CreatePurchaseRequest} from "@/types/api/request";
import {Getter} from "vuex-class";
import SelectProductDialog from "@/components/Product/SelectProductDialog.vue";
import {ApiResponse, CommonResponse, Supplier, Warehouse} from "@/types/api";

@Component({
  components: {SelectProductDialog},
})
export default class AddPurchaseDialog extends Mixins(AsModal) {
  @Getter("permission/id") readonly id!: number;
  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.api.getSupplyList().then((res) => (this.supplyList = res));
      this.api.getSalesList().then((res) => (this.salesList = res));
      this.api.getPurchaseSettleList().then((res) => (this.settleList = res));
      this.api.getWarehouseList().then((res) => (this.stockList = res));
    } else {
      this.purchaseOrderFormData = {...initialCreatePurchase};
      this.productList = [];
    }
  }

  displaySelectProductDialog: boolean = false;
  stockList: ApiResponse<Warehouse>[] = [];
  supplyList: ApiResponse<Supplier>[] = [];
  salesList: CommonResponse = [];
  settleList: CommonResponse = [];
  productList = [];
  purchaseOrderFormData: CreatePurchaseRequest = {...initialCreatePurchase};

  purchaseTablePaginationData = {
    totalRows: 400,
    currentPage: 1,
    currentPageSize: 20,
    pageSizes: [20, 50, 100, 200],
  };

  save(): void {
    this.purchaseOrderFormData.business_date = formatDate(
      this.purchaseOrderFormData.business_date,
      DateFormat.YMDDashed,
    );
    this.purchaseOrderFormData.user_id = this.id;
    const specs = this.productList.map((product)=> {return {
      spec_id: product.id,
      count: Number(product.amount)
    }})
    const params = {purchase: this.purchaseOrderFormData, specs}
    this.api.addPurchase(params).then(() => {
      this.refresh();
      this.dismiss();
    });
  }

  updateSelectedProducts(selected: any[]): void {
    this.productList = selected;
    this.displaySelectProductDialog = false;
  }

  resetForm() {
    this.$nextTick(() => {
      const ref: any = this.$refs["purchaseFormRef"];
      ref.resetFields();
    });
    this.purchaseOrderFormData = {...initialCreatePurchase};
  }

  deleteProduct(index) {
    this.productList.splice(index, 1);
  }

  displayTotalPrice(price, amount):number {
    if( Number.isNaN(amount)) {
      amount = 0;
    }
    return price * amount 
  }
}
